const getToken = async (tokenFromAccount) => {
	const res = await fetch(
		`https://cors-anywhere.herokuapp.com/https://login.questrade.com/oauth2/token?grant_type=refresh_token&refresh_token=${tokenFromAccount}`,
		{
			method: "GET",
			headers: {
				// "Content-Type": "application/json",
				// Accept: "application/json",
				// Authorization: "Bearer Zjv3AeNkpu-QvtopoPF0lVJK6SHbsfGx0",
			},
			// mode: "cors",
			// credentials: "include",
		}
	);
	console.log("res", res);
	console.log("res.json", res.json());
	alert(res);
	alert(res.json());
};

const getAccountInfomation = async (type, token) => {
	const res = await fetch(
		`https://cors-anywhere.herokuapp.com/https://api07.iq.questrade.com/v1/accounts/52130491/${type}`,
		{
			method: "GET",
			headers: {
				// "Content-Type": "application/json",
				// Accept: "application/json",
				Authorization: `Bearer ${token}`,
			},
			// mode: "cors",
			// credentials: "include",
		}
	);
	console.log(res, res.body);
	let result = await res.json();
	console.log("position", result);
	return result.positions;
};

export { getToken, getAccountInfomation };
